.modules-container {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;

    width: 100%;
    height: 100vh;
    background: linear-gradient(to bottom, #FFFFFF 0%, #493E6A80 80%);
}

.child-container {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;

    width: 90%;
    max-width: 45rem;

    overflow-y: auto;
    height: 90%;
}

.referance {
    display: flex;
    flex-direction: column;
    justify-content: start;

    width: 100%;
    margin: 1rem 0;
}

.welcome {
    width: fit-content;
    margin: 0;
 
    /* Hoş Geldiniz! */
 

font-family: 'Poppins';
font-style: normal;
font-weight: 600;
font-size: 20px;
line-height: 30px;
/* identical to box height */
display: flex;
align-items: center;

color: #2A428C;


/* Inside auto layout */
flex: none;
order: 0;
flex-grow: 0;

